



























import Vue, { VueConstructor } from 'vue';
import { VModel, VM } from '@/helpers';
import UserInformationVue from './UserInformation.vue';
import { store, actions } from '../../store';
// import UserInformationVue from '@/AppHeader/UserInformation.vue';

export default VM().extend({
    components: {
        UserInformationVue,
    },
    computed: {
        menus() {
            return ['任務', '排行', '背包', '好友', '聊天', '摸彩', '客服', '拉彩', '導引', '設定'];
        },
        game() {
            return store.getters.game;
        },
    },
    methods: {
        leaveGame() {
            actions.leaveGame();
        },
    },
});
