import Vue from 'vue';
import Vuex, { Store } from 'vuex';

Vue.use(Vuex);

declare class MyStore<S> extends Store<S> {
    public readonly getters: IStore.Getters;
}
export const store: MyStore<IStore.RootState> = new Vuex.Store<IStore.RootState>({
    state: {
        member: {
            id: 1,
            name: 'ㄨ屁孩無限大ㄨ',
            // avatar: 'https://i.pravatar.cc/300',
            avatar: '',
            level: 1,
            sliverPoint: 2000,
            goldPoint: 25000,
        },
        launchGameId: 0,
        games: [
            { id: 1, url: 'http://35.229.172.165/Game/2124/zh_tw/A/?demo=true', name: 'PEPPER RARTY' },
            { id: 2, url: 'http://35.229.172.165/Game/2127/zh_tw/A/?demo=true', name: 'GOLDEN EGYPT' },
            { id: 3, url: 'http://35.229.172.165/Game/2129/zh_tw/A/?demo=true', name: '着物女子' },
        ],
    },
    mutations: {
        LAUNCH_GAME(state, id: number) {
            state.launchGameId = id;
        },
    },
    getters: {
        game(state) {
            const { games, launchGameId } = state;
            return games.find((g) => g.id === launchGameId);
        },
    },
});
export const actions = {
    launchGame(gid: number) {
        store.commit('LAUNCH_GAME', gid);
    },
    leaveGame() {
        store.commit('LAUNCH_GAME', 0);
    },
};
