














import Vue from 'vue';
import AppHeaderVue from './components/AppHeader/AppHeader.vue';
import AppFooterVue from './components/AppFooter/AppFooter.vue';
import AppMainVue from './components/AppMain/AppMain.vue';

const DisplayWidth = 16;
const DisplayHeight = 9.0;
const DisplaySize = DisplayWidth / DisplayHeight;
const DefaultWidth = 800;
const DefaultFontSize = 16;

const $html = document.querySelector('html') as HTMLElement;

export default Vue.extend({
    components: {
        AppHeaderVue,
        AppMainVue,
        AppFooterVue,
    },
    name: 'App',
    data: () => ({
        appWidth: 0,
        appHeight: 0,
        fontSize: 0,
    }),
    computed: {
        appStyle() {
            return {
                width: `${this.appWidth}px`,
                height: `${this.appHeight}px`,
            };
        },
    },
    mounted() {
        this.onAppResize();
    },
    methods: {
        onAppResize() {
            const ww = Math.max(600, window.innerWidth);
            const wh = window.innerHeight;
            if (ww / wh > DisplaySize) {
                this.appHeight = wh;
                this.appWidth = (wh / DisplayHeight) * DisplayWidth;
            } else {
                this.appWidth = ww;
                this.appHeight = (ww / DisplayWidth) * DisplayHeight;
            }

            $html.style.fontSize = `${(this.appWidth / DefaultWidth) * DefaultFontSize}px`;
        },
    },
});
