import TWEEN from '@tweenjs/tween.js';

function animate(time: number) {
    requestAnimationFrame(animate);
    TWEEN.update(time);
}
requestAnimationFrame(animate);
export function tween(object?: any, group?: TWEEN.Group) {
    return new TWEEN.Tween(object, group);
}

const t = new TWEEN.Tween();

export const Easing = TWEEN.Easing;
export const Group = TWEEN.Group;
export default TWEEN;
