




















import Vue from 'vue';
import { VM } from '../../helpers';
import { tween } from '../../libs/tween';
export default VM<{
    $refs: {
        marqueeWrapper: HTMLDivElement;
    };
}>().extend({
    data: (): {
        paused: boolean;
        wrapperWidth: number;
        marqueeWidth: number;
        marqueeTween: TWEEN.Tween | null;
        speed: number;
        animate: { value: number };
    } => ({
        paused: false,
        wrapperWidth: 0,
        marqueeWidth: 0,
        marqueeTween: null,
        speed: 100,
        animate: {
            value: 0.0,
        },
    }),
    computed: {
        messages() {
            return [
                '#1. 這裡是跑馬燈 123',
                '#2. 這裡是跑馬燈 123',
                '#3. 這裡是跑馬燈 123',
                '#4. 這裡是跑馬燈 123',
                '#5. 這裡是跑馬燈 123',
                '#6. 這裡是跑馬燈 123',
            ];
        },
        running(): boolean {
            return (this.marqueeTween && this.marqueeTween.isPlaying()) || false;
        },
        moveWidth(): number {
            return this.marqueeWidth + this.wrapperWidth;
        },
        duration(): number {
            return Math.floor((this.moveWidth / this.speed) * 1000);
        },
        marqueeStyle(): { [key: string]: string } {
            return {
                transform: `translateX(${this.wrapperWidth - this.moveWidth * this.animate.value}px)`,
            };
        },
    },
    watch: {
        paused(paused: boolean) {
            if (!this.marqueeTween) {
                return;
            }
            if (paused) {
                this.marqueeTween.pause();
            } else {
                this.marqueeTween.resume();
            }
        },
    },
    mounted() {
        this.$nextTick(() => {
            this.onResize();
        });
        // this.start();
    },
    methods: {
        onResize() {
            this.wrapperWidth = this.$el.getBoundingClientRect().width;
            this.marqueeWidth = this.$refs.marqueeWrapper.getBoundingClientRect().width;
        },
        start() {
            if (!this.marqueeTween) {
                this.marqueeTween = tween(this.animate).to({ value: 1.0 }, this.duration);
            }
            this.marqueeTween.stop();
            this.animate.value = 0.0;
            this.marqueeTween.start();
        },
    },
});
