







































import Vue from 'vue';
import { store, actions } from '../../store';
import MarqueeVue from './Marquee.vue';
export default Vue.extend({
    components: {
        MarqueeVue,
    },
    computed: {
        game() {
            return store.getters.game;
        },
        games() {
            const chunks: IStore.Game[][] = [[], []];
            store.state.games.forEach((game, i) => {
                chunks[i % 2].push(game);
            });
            return chunks;
        },
    },
    methods: {
        launchGame(game: IStore.Game) {
            actions.launchGame(game.id);
        },
    },
});
